<template>
  <div>
    <div class="dw-dcs-main-title" >
      <div style="padding: 5px 10px;">
        <el-row type="flex">
          <el-col :span="18"><div style="font-size: 14px;"><strong></strong></div></el-col>
          <el-col :span="6" style="text-align: right;padding-right: 16px;" ><el-button type="primary" size="small" @click="handleExport" ></el-button></el-col>
        </el-row>
      </div>
      <div style="padding: 5px 10px;">
        <el-form :inline="true" :model="formInline" label-width="50px" class="dw-form-inline" size="medium" >
          <el-form-item label="" >
            <el-date-picker
              v-model="formInline.anTime"
              type="daterange"
              range-separator=""
              start-placeholder=""
              end-placeholder=""
              value-format="yyyy-MM-dd HH:mm:ss"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="IP" >
            <el-input v-model="formInline.ip" placeholder="" clearable></el-input>
          </el-form-item>
          <el-form-item label="" >
            <el-input v-model="formInline.city" placeholder="" clearable></el-input>
          </el-form-item>
          <el-form-item>
            <el-button style="margin-left: 20px;" @click="resetSearch"></el-button>
            <el-button type="primary" @click="queryList(1)"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <el-table
      :data="tableData"
      stripe
      style="width: 100%">
      <el-table-column type="selection" width="45"></el-table-column>
      <el-table-column label="" width="65" >
        <template slot-scope="scope">
          <div>{{ pageSize*(currentPage-1)+scope.$index+1 }}</div>
        </template>
      </el-table-column>
      <el-table-column label="IP" prop="answerCommon.anIp.ip"></el-table-column>
      <el-table-column label="" prop="answerCommon.anIp.city">
        <template slot-scope="scope">
          <div>{{ scope.row.answerCommon.anIp.city!=null ? scope.row.answerCommon.anIp.city: '-' }}</div>
        </template>
      </el-table-column>
      <el-table-column label="" >
        <template slot-scope="scope">
          <div>{{ scope.row.answerCommon.anTime.endAnDate }}</div>
        </template>
      </el-table-column>
      <el-table-column label="" >
        <template slot-scope="scope">
          <div>{{ scope.row.answerCommon.anTime.totalTime!=null ? secondsToHms(Math.floor(scope.row.answerCommon.anTime.totalTime/1000)):0 }}</div>
        </template>
      </el-table-column>
      <el-table-column label="" >
        <template slot-scope="scope">
          <div>{{ scope.row.answerCommon.anState.anQuNum!=null ? scope.row.answerCommon.anState.anQuNum:0 }}&nbsp;</div>
        </template>
      </el-table-column>
      <el-table-column label="" width="160" >
        <template slot-scope="scope">
          <el-button-group>
            <el-tooltip effect="dark" content="" placement="top">
              <el-button size="mini" icon="el-icon-view" @click="answerView(scope.row)"></el-button>
            </el-tooltip>
            <el-tooltip effect="dark" content="" placement="top">
              <el-button size="mini" icon="el-icon-delete" @click="handleDelete(scope.$index, scope.row)"></el-button>
            </el-tooltip>
          </el-button-group>
        </template>
      </el-table-column>
    </el-table>
    <div class="dw-pagination">
      <el-pagination
        :page-size="pageSize"
        :current-page="currentPage"
        :total="total"
        background
        layout="total, prev, pager, next"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
    </div>
    <el-dialog :visible.sync="dialogFormVisible" append-to-body title="" width="40%" >
      <div style="line-height: 30px;"></div>
      <div style="color: grey;line-height: 30px;font-size: 12px;"><span></span></div>
      <el-switch
        v-model="expUpQu"
        active-text=""
        inactive-text="Excel"
        active-value="1"
        inactive-value="0">
      </el-switch>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="executeExportData"> </el-button>
      </div>
    </el-dialog>
    <dw-survey-answer-info-dialog ref="dwAnswerInfoDialog" @refresh-data="queryList(null)" ></dw-survey-answer-info-dialog>
  </div>
</template>

<script>
import {dwSurveyAnswerDelete} from '../../../../api/dw-survey'
import {dwSurveyAnswerListV6} from "../api/dw-survey-answer-data";
import DwSurveyAnswerInfoDialog from "./components/DwSurveyAnswerInfoDialog.vue";
import {secondsToHms} from "../../dw-utils/dw-common/dw-common-1";

export default {
  name: 'DwSurveyAnswerDataList',
  components: {DwSurveyAnswerInfoDialog},
  data () {
    return {
      tableData: [],
      pageSize: 10,
      currentPage: 1,
      total: 0,
      dialogFormVisible: false,
      expUpQu: 0,
      formInline: {
        ip: null,
        city: null,
        anTime: null
      }
    }
  },
  mounted () {
    this.queryList(1)
  },
  methods: {
    secondsToHms,
    handleGo (to) {
      this.$router.push(to)
    },
    handlePush: function (to) {
      this.$router.push(to)
    },
    handleDelete (index, row) {
      console.log(index, row)
      this.$msgbox.confirm('', '', {type: 'warning', confirmButtonText: ''}).then(() => {
        const data = {id: [row.id]}
        dwSurveyAnswerDelete(data).then((response) => {
          console.log(response)
          const httpResult = response.data
          if (httpResult.resultCode === 200) {
            this.$message.success('')
            this.queryList(1)
          } else {
            this.$message.error('')
          }
        })
      }).catch(() => {})
    },
    handleCurrentChange (val) {
      this.queryList(val)
    },
    handleExport () {
      this.dialogFormVisible = true
    },
    executeExportData () {
      const downUrl = `${process.env.DW_API_URL}${API.surveyAnswerExport}?surveyId=${this.$route.params.id}&expUpQu=${this.expUpQu}`
      this.dialogFormVisible = false
      window.location.href = downUrl
    },
    queryList (pageNo) {
      this.currentPage = pageNo
      const maxTotal = 10000
      if ((pageNo-1)*this.pageSize>=maxTotal) {
        this.$message({
          message: '1',
          type: 'warning',
          duration: 10000
        })
        this.currentPage = (maxTotal/this.pageSize)-1
      }
      // this.pageSize, pageNo, this.$route.params.id
      console.debug('anTime:', this.formInline.anTime)
      console.debug('anIp:', this.formInline.ip)
      console.debug('anCity:', this.formInline.city)
      const anTime = {bgAnDate: null, endAnDate: null}
      const formAnTime = this.formInline.anTime
      if (formAnTime !== null) {
        // "yyyy-MM-dd HH:mm:ss"
        anTime.bgAnDate = formAnTime[0]
        anTime.endAnDate = formAnTime[1]
      }
      const params = {surveyId: this.$route.params.id, pageNo: this.currentPage, bgAnDate: anTime.bgAnDate, endAnDate: anTime.endAnDate, ip: this.formInline.ip, city: this.formInline.city}
      dwSurveyAnswerListV6(params).then((response) => {
        const page = response.data
        this.pageSize = page.pageSize
        this.currentPage = page.pageNo
        this.total = page.totalItems
        this.tableData = page.result
        console.debug('dwSurveyAnswerListV6', response)
      })
    },
    resetSearch () {
      this.formInline = {ip: null, city: null, anTime: null}
      this.queryList(1)
    },
    answerView (row) {
      this.$refs.dwAnswerInfoDialog.openDialog(row)
    }
  }
}
</script>

<style scoped>
.dw-pagination{
  padding-top: 20px;
  text-align: right;
}
</style>



